.container {
  width: 80%;
  background-color: #F3F3F3;
  padding: 28px 20px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  align-items: center;
  row-gap: 10px;
  box-shadow: 0px 1px 1px 1px rgba(170, 178, 182, 0.79);
}
.container .blockForTitleAndIcon {
  display: flex;
  column-gap: 15px;
  align-items: center;
  justify-content: center;
}
.container .blockForTitleAndIcon h4 {
  font-size: 24px;
  color: #0F67EC;
  text-shadow: 0px 0px 1px #2c2c2c;
  position: relative;
  bottom: 2px;
}
.container .blockForTitleAndIcon .eyeIcon {
  font-size: 15px;
  color: #0F67EC;
}
.container .when {
  width: 100%;
  justify-content: center;
  display: flex;
  column-gap: 10px;
}
.container .when span {
  font-size: 18px;
  color: #2165a9;
  text-shadow: 0px 0px 1px #3b3b3b;
}

