.container {
  background-color: #E7F1F0;
  width: 75%;
  position: absolute;
  top: -100px;
  height: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  .line {
    position: absolute;
    top: -10px;
    height: 20px;
    width: 100%;
  }

  .title {
    color: #309AAC;
    font-size: 22px;
    text-shadow: 0px 0px 1px black;
    transition: 400ms;

    &:hover {
      cursor: pointer;
      text-decoration: underline;
    }
  }
}
