.container {
  background-color: rgba(241, 241, 241, 0.89);
  width: 100%;
  height: 580px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  flex-direction: column;
  border: 1px solid #a8a6a6;
  overflow: hidden;
  border-radius: 20px;
  padding: 0 0 40px 0;
  position: relative;

  img {
    width: 100%;
  }

  .blockForTimePublishedAndCategory {
    padding: 5px 0;
    width: 95%;
    display: flex;
    justify-content: space-around;
    box-sizing: border-box;
  }

  .blockForTitleAndShortDescriptionOfArticle {
    box-sizing: border-box;
    width: 95%;
    padding-bottom: 10px;
    border-bottom: 1px solid gray;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .btnReamMore {
    color: #545454;
    font-size: 16px;
    position: absolute;
    right: 40px;
    bottom: 20px;
    letter-spacing: 1px;
    transition: 450ms;
    font-weight: bold;

    &:hover {
      color: #153e9b;
    }
  }
}
