.container {
  width: 30%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  align-items: center;
}
.container h4 {
  text-align: center;
  font-size: 20px;
  margin-bottom: 5px;
  color: #1e1e1e;
}
.container span {
  text-align: center;
  font-size: 13px;
  color: #2d2d2d;
  font-weight: bold;
}

